<template>
  <aside class="swaps-item-sidebar">
    <div
      v-for="item in routes"
      :key="item.key"
      class="swaps-item-sidebar__route"
      :class="{'swaps-item-sidebar__route_active': $route.query.t === item.key}"
      @click="onSelectRoute(item)"
    >
      <Text size="16px" weight="500" line-height="19px">
        {{ t(item.title) }}
      </Text>
    </div>
  </aside>
</template>

<script>
import { ref } from 'vue'
import { useI18n } from '@/shared/plugins/i18n'
import { useModel } from '@/shared/utils/useModel'
import { useRouter, useRoute } from '@/shared/plugins/vue-router'
import Text from '@/shared/components/UIKit/Text/Text'

export default {
  name: 'SwapsItemSidebar',
  components: { Text },
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      required: true
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const route = useRoute()
    const router = useRouter()
    const { value: activeRoute } = useModel(props, emit)

    const routes = ref([
      {
        key: 'inputs',
        component: 'SwapsInputs',
        title: 'swaps.tabs.inputs'
      },
      {
        key: 'outputs',
        component: 'SwapsOutputs',
        title: 'swaps.tabs.outputs'
      },
      {
        key: 'comments',
        component: 'SwapsComments',
        title: 'swaps.tabs.comments'
      }
    ])

    const onSelectRoute = (tab) => {
      activeRoute.value = tab
      router.push({
        ...route,
        query: {
          t: tab.key
        }
      })
    }

    onSelectRoute(
      routes.value.find(item => item.key === route.query.t) ||
      routes.value[0]
    )

    return {
      t,
      routes,
      activeRoute,
      onSelectRoute
    }
  }
}
</script>

<style lang="scss">
.swaps-item-sidebar {

  &__route {
    width: 220px;
    height: 48px;
    padding: 15px 20px;
    border-radius: 4px;
    cursor: pointer;

    &_active {
      background-color: map-get($colors, "default");
    }
  }
}
</style>
